import React, { Fragment, useEffect, useState } from 'react'
import Question from './components/Question'
import Button from '@material-ui/core/Button';

import defineSelectedAnswer from '../../lib/defineSelectedAnswer'
import questionFormation from '../../lib/questionFormation'
import beep from '../../lib/beep'
// import { logics } from '../../lib/testLogic'
// import mainLogic from '../../lib/mainLogic'

const KEY_TYPE = 'keyup'
const STEP_DELAY = 200
const MOVE_DELAY = 0

const ANSWER_SELECTED = 1
const RESET_RESULTS = 2
const CONFIRM_QUESTION = 3
const SKIP = 4

const PollDrive = ({ poll, logics, mainDiv, setCurrentQuestion }) => {
  const questionsLimit = poll.questions.length
  const [question, setQuestion] = useState(null)
  const [userSettings, setUserSettings] = useState({
    codesShow: true
  })
  const [logic, setLogic] = useState(logics)
  const [count, setCount] = useState(0)
  const [results, setResults] = useState(
    {
      pool: []
    }
  )
  const keyUpHandler = ({ target, keyCode }) => {
    if (target.nodeName == 'BODY') {
      const nextStep = defineSelectedAnswer(keyCode)
      switch (nextStep.do) {
        case ANSWER_SELECTED: {
          mainLogic(nextStep.trueCode)
          return
        }
        case CONFIRM_QUESTION: {
          confirmResults()
          return
        }
        case RESET_RESULTS: {
          resetAnswers()
          return
        }
        case SKIP: {
          return                                                    //   ,    
        }
      }
    } else {
      console.log('input');
    }
  }

  const resetAnswers = () => {
    const savedCodes = results[question.id].data.reduce((acum, val) => [...acum, val.answerCode], [])
    setResults(prevState => ({
      ...prevState,
      [question.id]: {
        ...prevState[question.id],
        data: []
      },
      pool: prevState.pool.filter(obj => {
        return savedCodes.includes(obj) ? false : true
      })
    }))
    setQuestion(prevState => ({
      ...prevState,
      answers: prevState.answers.map(
        answer => true ? { ...answer, selected: false, showFreeAnswer: false } : answer
      )
    }))
    const arrayOfCodes = question.answers.reduce((acum, val) => [...acum, val.code], [])
    //            !!!!!!
    const arrayToCheck = logic.criticalExclude
      .filter(obj => !arrayOfCodes.includes(obj.code))
      .reduce((acum, val) => [...acum, ...val.explPool], [])
    //    
    if (arrayToCheck.length) {
      setQuestion(prevState => ({
        ...prevState,
        answers: prevState.answers.map(
          answer => arrayToCheck.includes(answer.code) ? answer : { ...answer, disabled: false }
        )
      }))
    } else {
      //      
      setQuestion(prevState => ({
        ...prevState,
        answers: prevState.answers.map(
          answer => ({ ...answer, disabled: false })
        )
      }))
    }
    // !!!!!
    setLogic(prevState => ({
      ...prevState,
      criticalExclude: prevState.criticalExclude.filter(obj => !arrayOfCodes.includes(obj.code))
    }))
  }

  const confirmResults = () => {
    if (results[question.id].data.length) {
      goToNext()
      return
    }
    beep()
  }

  const mainLogic = (code) => {
    const trueCode = +code
    const keyCodesPool = question.keyCodesPool
    const selectedAnswer = question.answers.filter(obj => obj.keyCode === trueCode)[0]
    //   
    if (trueCode === 39) {
      setTimeout(() => {
        goToNext()
      }, MOVE_DELAY)
      return
    } else if (trueCode === 37) {
      setTimeout(() => {
        goToPrevious()
      }, MOVE_DELAY)
      return
    }

    if (keyCodesPool.includes(trueCode)) {                                  //       
      if (results.pool.includes(selectedAnswer.code)) {
        //       =>    =>   
        setResults(prevState => ({
          ...prevState,
          [question.id]: {
            ...prevState[question.id],
            data: prevState[question.id].data.filter(el => el.answerCode !== selectedAnswer.code)
          },
          pool: prevState.pool.filter(el => el !== selectedAnswer.code)
        }))
        setQuestion(prevState => ({
          ...prevState,
          answers: prevState.answers.map(
            answer => answer.code === selectedAnswer.code ? { ...answer, selected: false, showFreeAnswer: false } : answer
          ),
        }))
        //            !!!!!!
        const arrayToCheck = logic.criticalExclude
          .filter(obj => obj.code !== selectedAnswer.code)
          .reduce((acum, val) => [...acum, ...val.explPool], [])
        //    
        if (arrayToCheck.length) {
          setQuestion(prevState => ({
            ...prevState,
            answers: prevState.answers.map(
              answer => arrayToCheck.includes(answer.code) ? answer : { ...answer, disabled: false }
            )
          }))
        } else {
          //      
          setQuestion(prevState => ({
            ...prevState,
            answers: prevState.answers.map(
              answer => ({ ...answer, disabled: false })
            )
          }))
        }
        setLogic(prevState => ({
          ...prevState,
          criticalExclude: prevState.criticalExclude.filter(obj => obj.code !== selectedAnswer.code)
        }))
        return
      }
      //           (  - )
      if (logic.unique.includes(selectedAnswer.code)) {
        if (results[question.id].data.length) {
          beep()
          return
        }
      }
      //         
      if (selectedAnswer.disabled || results[question.id].data.length >= question.limit) {
        beep()
        return
      }
      //    
      if (selectedAnswer.freeAnswer) {
        setQuestion(prevState => ({
          ...prevState,
          answers: prevState.answers.map(
            answer => answer.keyCode === selectedAnswer.keyCode ? { ...answer, selected: true, showFreeAnswer: true } : answer
          )
        }))
        return
      }
      const result = {
        answerCode: selectedAnswer.code,
        freeAnswer: false,
        freeAnswerText: ''
      }
      let newResultState = Object.assign({}, results);
      newResultState[question.id].data.push(result)
      newResultState.pool.push(selectedAnswer.code)
      setResults(newResultState)

      setQuestion(prevState => ({
        ...prevState,
        answers: prevState.answers.map(
          answer => answer.keyCode === trueCode ? { ...answer, selected: true } : answer
        ),
      }))
      //    (  - ) ->   
      if (logic.unique.includes(selectedAnswer.code)) {
        setQuestion(prevState => ({
          ...prevState,
          answers: prevState.answers.map(
            answer => answer.keyCode === trueCode ? answer : { ...answer, disabled: true }
          )
        }))
        const poolOfExcludedCodes = question.answers.filter(answer => answer.code !== selectedAnswer.code).map(answer => answer.code)
        setLogic(prevState => ({
          ...prevState,
          criticalExclude: [
            ...prevState.criticalExclude,
            { code: selectedAnswer.code, explPool: poolOfExcludedCodes }
          ]
        }))
      }
      //   
      if (results[question.id].data.length >= question.limit) {
        //  
        setTimeout(() => {
          goToNext()
        }, STEP_DELAY)
        return
      }
    } else {
      beep()
    }
  }

  const goToNext = () => {
    if (count < questionsLimit - 1) {
      setCount(count + 1)
    } else {
      setCount(0)
    }
  }

  const goToPrevious = () => {
    if (count === 0) return
    setCount(count - 1)
  }

  useEffect(() => {
    document.addEventListener(KEY_TYPE, keyUpHandler)
    return () => {
      document.removeEventListener(KEY_TYPE, keyUpHandler)
    };
  })

  //     ID   !!!!!!!!!!!!!!!!!!!!!!!!

  useEffect(() => {
    //  ,         +   
    const newQuestion = questionFormation(poll, count, results, logic, setResults);
    if (!newQuestion) return
    if (newQuestion.next) {
      setResults(prevState => ({
        ...prevState
      }))
      //   
      // ???????????????????
      setCount(count + 1)
      return
    }
    setQuestion(newQuestion.data)
    setCurrentQuestion(newQuestion.data)
  }, [count])

  const clickHandler = (target) => {
    const code = target.dataset.code
    const selectedAnswer = question.answers.filter(obj => obj.keyCode === +code)[0]
    // ,       
    if (selectedAnswer.freeAnswer && selectedAnswer.selected) {
      return
    }
    mainLogic(code)
  }

  const blurHandler = (e) => {
    const value = e.currentTarget.value
    const selectedCode = e.currentTarget.dataset.code
    const selectedKeyCode = e.currentTarget.dataset.keycode
    if (value === '') {
      //     ->       
      setResults(prevState => ({
        ...prevState,
        [question.id]: {
          ...prevState[question.id],
          data: prevState[question.id].data.filter(el => el.answerCode !== selectedCode)
        },
        pool: prevState.pool.filter(el => el !== selectedCode)
      }))
      setQuestion(prevState => ({
        ...prevState,
        answers: prevState.answers.map(
          answer => answer.code === selectedCode ? { ...answer, selected: false, showFreeAnswer: false } : answer
        )
      }))
      const arrayToCheck = logic.criticalExclude
        .filter(obj => obj.code !== selectedCode)
        .reduce((acum, val) => [...acum, ...val.explPool], [])
      //    
      if (arrayToCheck.length) {
        setQuestion(prevState => ({
          ...prevState,
          answers: prevState.answers.map(
            answer => arrayToCheck.includes(answer.code) ? answer : { ...answer, disabled: false }
          )
        }))
      } else {
        //      
        setQuestion(prevState => ({
          ...prevState,
          answers: prevState.answers.map(
            answer => ({ ...answer, disabled: false })
          )
        }))
      }
      setLogic(prevState => ({
        ...prevState,
        criticalExclude: prevState.criticalExclude.filter(obj => obj.code !== selectedCode)
      }))
      return
    }
    //    ->  
    const result = {
      answerCode: selectedCode,
      freeAnswer: false,
      freeAnswerText: value
    }
    if (results.pool.includes(selectedCode)) {
      setResults(prevState => ({
        ...prevState,
        [question.id]: {
          ...prevState[question.id],
          data: prevState[question.id].data.map(
            answer => answer.answerCode === selectedCode ? { ...answer, freeAnswerText: value } : answer
          )
        },
        pool: [
          ...prevState.pool,
          selectedCode
        ]
      }))
    } else {
      setResults(prevState => ({
        ...prevState,
        [question.id]: {
          ...prevState[question.id],
          data: [
            ...prevState[question.id].data,
            result
          ]
        },
        pool: [
          ...prevState.pool,
          selectedCode
        ]
      }))
    }
    //    (  - ) ->   
    if (logic.unique.includes(selectedCode)) {
      setQuestion(prevState => ({
        ...prevState,
        answers: prevState.answers.map(
          answer => answer.keyCode === +selectedKeyCode ? answer : { ...answer, disabled: true }
        )
      }))
      const poolOfExcludedCodes = question.answers.filter(answer => answer.code !== selectedCode).map(answer => answer.code)
      setLogic(prevState => ({
        ...prevState,
        criticalExclude: [
          ...prevState.criticalExclude,
          { code: selectedCode, explPool: poolOfExcludedCodes }
        ]
      }))
    }
    if (results[question.id].data.length >= question.limit - 1) {         // -1 ..    
      setTimeout(() => {
        goToNext()
      }, STEP_DELAY)
    }
  }
  const codesShow = (e) => {
    setUserSettings(prevState => ({
      ...prevState,
      codesShow: !prevState.codesShow
    }))
  }
  const multipleHandler = (value, codes) => {
    console.log(value, codes);
    if (value.length) {
      const codesPool = value.map(val => val.code)
      const resultsPool = value.map(val => {
        return {
          answerCode: val.code,
          // keyCode: val.keyCode,
          freeAnswer: false,
          freeAnswerText: ''
        }
      })
      const clearPool = results.pool.filter(val => !codes.includes(val))
      setResults(prevState => ({
        ...prevState,
        [question.id]: {
          ...prevState[question.id],
          data: [
            ...resultsPool
          ]
        },
        pool: [
          ...clearPool,
          ...codesPool
        ]
      }))
      if (value.length) {
        setTimeout(() => {
          goToNext()
        }, STEP_DELAY)
      }
      return
    }
    setResults(prevState => ({
      ...prevState,
      [question.id]: {
        ...prevState[question.id],
        data: []
      },
      pool: prevState.pool.filter(obj => !codes.includes(obj))
    }))
  }

  return (
    <Fragment>
      <label>
        <input type="checkbox" onChange={codesShow} defaultChecked={userSettings.codesShow} />
          
      </label>
      {question &&
        <Question
          count={count}
          question={question}
          settings={userSettings}
          clickHandler={clickHandler}
          blurHandler={blurHandler}
          multipleHandler={multipleHandler}
        />
      }
      <Button onClick={goToPrevious} variant="contained" style={{ margin: '5px' }}></Button>
      <Button onClick={goToNext} variant="contained" style={{ margin: '5px' }}></Button>
    </Fragment>
  )
}

export default PollDrive