import { keycodes } from './keycodes'

//  ,       
const questionFormation = (poll, count, result, logic, setResults) => {
  const question = poll.questions[count]
  let keyCodesPool = []

  //       (  - )
  const visibleAnswers = logic.invisible ? question.answers.filter(obj => !logic.invisible.includes(obj.code))
    : question.answers

  //   r  
  const arrayOfBlockedCodes = []
  // const arrayOfBlockedCodes = logic.criticalExclude.reduce((acum, val) => [...acum, ...val.explPool], [])

  let newAnswers = visibleAnswers.map((obj, i) => {
    //    - 
    let suffix = {
      freeAnswer: false,
      showFreeAnswer: false,
      freeAnswerText: '',
      focus: false,
      selected: false,
      disabled: false
    }
    if (visibleAnswers.length > keycodes.length) {
      keyCodesPool[i] = [i, i + 1]
    } else {
      keyCodesPool[i] = keycodes[i][1]
      suffix = {
        ...suffix,
        showIndex: keycodes[i][0],
        keyCode: keycodes[i][1],
      }
    }
    //   !
    if (result.pool.includes(obj.code)) {
      suffix = {
        ...suffix,
        selected: true
      }
    }
    //     (  - )
    if (arrayOfBlockedCodes.includes(obj.code)) {
      suffix = {
        ...suffix,
        disabled: true
      }
    }
    //     (  -  )
    if (logic.freeAnswers.includes(obj.code)) {
      suffix = {
        ...suffix,
        freeAnswer: true,
        focus: true
      }
    }
    if (result[question.id]) {
      let data = result[question.id].data
      for (let i = 0; i < data.length; i++) {
        if (obj.code === data[i].answerCode && data[i].freeAnswerText !== '') {
          suffix = {
            ...suffix,
            freeAnswer: true,
            showFreeAnswer: true,
            freeAnswerText: data[i].freeAnswerText,
            focus: false
          }
        }
      }
    }
    const resultQ = { ...obj, ...suffix }
    return resultQ
  })

  //    - 
  let qSuffix = {
    mega: false,
    megaMltp: false,
    multiple: false                                      //   
  }

  if (question.limit > 1) {
    qSuffix = {
      ...qSuffix,
      multiple: true
    }
  }

  //       ->  ,   multiple select
  if (visibleAnswers.length > keycodes.length) {
    qSuffix = {
      ...qSuffix,
      mega: true,
      megaMltp: question.limit
    }
  } else {
    qSuffix = {
      ...qSuffix,
      keyCodesPool
    }
  }

  if (result[question.id] === undefined) {
    setResults(prevState => ({
      ...prevState,
      [question.id]: {
        data: [],
        skip: false
      }
    }))
  }
  const data = {
    ...question,
    ...qSuffix,
    answers: newAnswers,

  }
  // console.log(data);
  return {
    next: false,
    data
  }
}

export default questionFormation